		blurs(content, url, method, data) {
			var self = this;
			uni.showModal({
				title: '提示',
				content: content,
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: url,
							method: method,
							data: data,
							header: {
								Authorization: 'Bearer ' + uni.getStorageSync('token')
							},
							success: (res) => {
								self.aa = false
								self.bb = false
								var res = res.data
								if (res.code == 1) {
									uni.showToast({
										title: res.info,
										icon: "none"
									});
									self.userinfo();
								} else {
									uni.showToast({
										title: res.info,
										icon: "none"
									});
									self.username = uni.getStorageSync('member').nickname;
								}
							}

						});
						console.log('用户点击确定');
					} else if (res.cancel) {
						self.username = uni.getStorageSync('member').nickname; //改变已输入的昵称
						console.log('用户点击取消');
					}
				}

			});
			console.log("blur");
		},
		changname() {
			var self = this;
			uni.showModal({
				title: '提示',
				content: '是否确认修改昵称!',
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: service.api.set.modifyingNickname,
							method: "POST",
							data: {
								nickname: self.username
							},
							header: {
								Authorization: 'Bearer ' + uni.getStorageSync('token')
							},
							success: (res) => {
								var res = res.data
								if (res.code == 1) {
									uni.showToast({
										title: res.info,
										icon: "none"
									});
									self.userinfo();
								} else {
									uni.showToast({
										title: res.info,
										icon: "none"
									});
									self.username = uni.getStorageSync('member').nickname;
								}
							}

						});
						console.log('用户点击确定');
					} else if (res.cancel) {
						self.username = uni.getStorageSync('member').nickname; //改变已输入的昵称
						console.log('用户点击取消');
					}
				}

			});
			console.log("comfirm");
		},
		changimg() {
			var self = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (chooseImageRes) => {
					console.log(chooseImageRes)
					const tempFilePaths = chooseImageRes.tempFilePaths;
					self.userimg = tempFilePaths[0]
					uni.uploadFile({
						url: service.api.main.avatar, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
						filePath: tempFilePaths[0],
						name: 'file',
						success: (res) => {
							uni.showToast({
								title: JSON.parse(res.data).info,
								icon: 'none'
							});
						}
					});
				}
			});


		},
		upimg() {
			var self = this;

			return Promise.resolve(self.userimg)
		},
		userinfo() { //获取会员信息
			var self = this;
			service.auth(this, service.api.home.info, 'GET', '', function(self, res) {
				console.log(res, "userinfo")
				var member = res.data.member;
				uni.setStorageSync("member", res.data.member);
				self.phonenum = member.account;
				self.username = member.nickname;
				self.userimg = member.avatar;
				self.levelname = member.levelname;
				self.add_time = member.add_time;
			});
		},

		paytype() { //获取支付收款信息
			var self = this;
			service.auth(this, service.api.set.paytype, 'GET', {//支付宝
				// status: 1
				type:1
			}, function(self, res) {
				// console.log(res.data, "userinfo")
				self.zhifubname = res.data.titleofaccount;
				self.zhifubphone = res.data.account;
				
			});
			service.auth(this, service.api.set.paytype, 'GET', {//银行卡
				// status: 1
				type:2
			}, function(self, res) {
				console.log(res.data, "userinfo")
				if(res.data){
					self.bankuser = res.data.titleofaccount;
					self.bankname = res.data.name;
					self.banknum = res.data.account
				}
			});
			service.auth(this, service.api.set.paytype, 'GET', {//钱包地址
				// status: 1
				type:3
			}, function(self, res) {
				console.log(res.data, "userinfo")
				if(res.data){
					self.wallet = res.data.account
				}
			});
		}
